/* apply a natural box layout model to all elements, but allowing components to change */
html {
    box-sizing: border-box;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    font-family: "Open Sans", sans-serif;
}

h1,
h2,
h3,
h4 {
    font-family: "Exo 2", sans-serif;
}
